.navbar {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 10px;
  position: fixed;
  top: 0;
  width: 100%;
  background-color: black;
  z-index: 100;
}

.link {
  color: green;
  text-decoration: none;
  font-size: 1.2rem;
}

.link:hover {
  text-shadow: 0 0 10px green;
}
